import { FaEye } from "react-icons/fa";
import { IoCloseCircleOutline } from "react-icons/io5";
import { ModalContainer } from "../../Styles/DataRoomStyle.js";
import React, { useState, useEffect } from "react";
import { NumericFormat } from "react-number-format";
import axios from "axios";

const InvestNowPopup = ({ onClose, records }) => {
  const capitalizeFirstLetter = (str) => {
    if (!str) return "";
    return str.charAt(0).toUpperCase() + str.slice(1);
  };

  const storedUsername = localStorage.getItem("InvestorData");
  const userLogin = JSON.parse(storedUsername);
  const [investment, setInvestment] = useState("");
  const [shares, setShares] = useState(0);
  const [submitted, setSubmitted] = useState(null);
  const [totalAllocatedShares, settotalAllocatedShares] = useState("0");
  const [successmessage, setSuccessmessage] = useState("");
  const [errr, seterrr] = useState(false);
  var apiURL = "https://blueprintcatalyst.com/api/user/investor/";

  useEffect(() => {
    getcheckInvestorStatus();
  }, []);

  const getcheckInvestorStatus = async () => {
    let formData = {
      investor_id: userLogin.id,
      company_id: records.company_id,
      roundrecord_id: records.id,
    };
    try {
      const res = await axios.post(
        apiURL + "getcheckInvestorStatus",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      if (res.data.result.length > 0) {
        var checkCode = res.data.result;
        seterrr(true);
        // Make sure the submitted state is properly set as string
        setSubmitted(String(checkCode[0].request_confirm));
      } else {
        setSubmitted(null);
        seterrr(false);
      }
    } catch (err) {
      console.error("Error fetching capital round data:", err);
      setSubmitted(null);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    let formData = {
      investor_id: userLogin.id,
      company_id: records.company_id,
      shares: parseInt(String(shares).toString().replace(/,/g, "")) || 0,
      created_by_id: records.created_by_id,
      roundrecord_id: records.id,
      investment_amount:
        parseFloat(String(investment).toString().replace(/,/g, "")) || 0,
    };

    try {
      const res = await axios.post(
        apiURL + "InvestorrequestToCompany",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );

      // Set submitted to "No" initially (pending confirmation)
      setSubmitted("No");
      setSuccessmessage("Investment request submitted successfully!");

      setTimeout(() => {
        setSubmitted("No"); // Keep it as "No" until confirmed by company
      }, 4500);
    } catch (err) {
      console.error("Error submitting investment:", err);
      setSubmitted(null);
    }
  };

  const pricePerShare =
    records.roundsize && records.issuedshares
      ? records.roundsize / records.issuedshares
      : 0;

  const handleInvestmentChange = (e) => {
    let amountStr = e.target.value;
    let amount = parseFloat(amountStr.replace(/,/g, "")) || 0;

    const pricePerShare =
      records.roundsize && records.issuedshares
        ? records.roundsize / records.issuedshares
        : 0;

    let requestedShares = pricePerShare > 0 ? amount / pricePerShare : 0;

    const availableShares =
      typeof totalAllocatedShares === "number"
        ? Math.max(0, records.issuedshares - totalAllocatedShares)
        : records.issuedshares;

    if (requestedShares > availableShares) {
      requestedShares = availableShares;
      amount = requestedShares * pricePerShare;
    }

    setInvestment(
      amount.toLocaleString("en-US", {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2,
      })
    );
    setShares(requestedShares);
  };

  if (!records) return null;

  // Professional color palette
  const colors = {
    primary: "#F63C3F",
    primaryHover: "#D42C2F",
    primaryLight: "#FEEBEB",
    success: "#10B981",
    successLight: "#ECFDF5",
    info: "#3B82F6",
    infoLight: "#EFF6FF",
    textDark: "#1F2937",
    textSecondary: "#6B7280",
    border: "#E5E7EB",
    background: "#F9FAFB",
  };

  // Debug log to check the submitted state
  console.log("Current submitted state:", submitted, typeof submitted);

  return (
    <div
      className="main_popup-overlay"
      style={{
        backgroundColor: "rgba(0, 0, 0, 0.5)",
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        zIndex: 1000,
      }}
    >
      <ModalContainer
        style={{
          backgroundColor: "white",
          borderRadius: "12px",
          padding: "24px",
          maxWidth: "600px",
          width: "90%",
          maxHeight: "90vh",
          overflow: "auto",
          boxShadow:
            "0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)",
          padding: "10px",
        }}
      >
        <div className="previous-section-summary mb-4 p-4 bg-white border rounded-3 shadow-sm">
          <div className="d-flex align-items-center mb-3 pb-2 border-bottom">
            <div className="d-flex align-items-center justify-content-between gap-3 w-100">
              <h3
                className="mb-0 fw-semibold"
                style={{ color: colors.textDark }}
              >
                Invest Now
              </h3>
              <button
                type="button"
                className="bg-transparent p-1 border-0"
                onClick={onClose}
                style={{ color: colors.textSecondary }}
              >
                <IoCloseCircleOutline size={24} />
              </button>
            </div>
          </div>

          <div className="row g-3">
            <div className="col-md-12">
              <div
                className="p-4 rounded-3 h-100"
                style={{
                  backgroundColor: colors.primaryLight,
                  border: `1px solid ${colors.border}`,
                }}
              >
                <span
                  className="small fw-semibold text-uppercase"
                  style={{ color: colors.textSecondary }}
                >
                  Investment Details
                </span>

                {/* Case 1: Submitted but pending confirmation (submitted === "No") */}
                {submitted === "No" && (
                  <div
                    className="alert mt-3"
                    style={{
                      backgroundColor: colors.successLight,
                      border: `1px solid ${colors.success}`,
                      color: colors.success,
                      borderRadius: "8px",
                      padding: "16px",
                    }}
                  >
                    <div className="d-flex align-items-center">
                      <span
                        style={{
                          backgroundColor: colors.success,
                          color: "white",
                          borderRadius: "50%",
                          width: "24px",
                          height: "24px",
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "center",
                          marginRight: "12px",
                          fontSize: "14px",
                          padding: "10px",
                        }}
                      >
                        ✓
                      </span>
                      <div>
                        <strong>Investment Submitted Successfully!</strong>
                        <p
                          className="mb-0 mt-1"
                          style={{ color: colors.textSecondary }}
                        >
                          Your investment commitment has been submitted. The
                          company will contact you shortly.
                        </p>
                      </div>
                    </div>
                  </div>
                )}

                {/* Case 2: Confirmed by company (submitted === "Yes") */}
                {submitted === "Yes" && (
                  <div
                    className="alert mt-3"
                    style={{
                      backgroundColor: colors.infoLight,
                      border: `1px solid ${colors.info}`,
                      color: colors.info,
                      borderRadius: "8px",
                      padding: "16px",
                    }}
                  >
                    <div className="d-flex align-items-center">
                      <span
                        style={{
                          backgroundColor: colors.info,
                          color: "white",
                          borderRadius: "50%",
                          width: "24px",
                          height: "24px",
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "center",
                          marginRight: "12px",
                          padding: "10px",
                          fontSize: "14px",
                        }}
                      >
                        ✓
                      </span>
                      <div>
                        <strong>Your Request Has Been Confirmed!</strong>
                        <p
                          className="mb-0 mt-1"
                          style={{ color: colors.textSecondary }}
                        >
                          Congratulations! Your investment request has been
                          confirmed by the company.
                        </p>
                      </div>
                    </div>
                  </div>
                )}

                {/* Case 3: Show investment form (submitted === null) */}
                {submitted === null && (
                  <form onSubmit={handleSubmit} className="mt-3" method="post">
                    <div className="form-group mb-4">
                      <label
                        className="form-label fw-semibold mb-2"
                        style={{ color: colors.textDark }}
                      >
                        Enter Investment Amount ($)
                      </label>

                      <NumericFormat
                        thousandSeparator={true}
                        decimalScale={2}
                        fixedDecimalScale={true}
                        allowNegative={false}
                        placeholder="Enter amount"
                        value={investment}
                        className="form-control"
                        onChange={handleInvestmentChange}
                        style={{
                          borderRadius: "8px",
                          border: `1px solid ${colors.border}`,
                          padding: "12px",
                          fontSize: "16px",
                        }}
                      />
                    </div>

                    <div className="form-group mb-4">
                      <label
                        className="form-label fw-semibold mb-2"
                        style={{ color: colors.textDark }}
                      >
                        Price Per Share
                      </label>
                      <div
                        className="form-control-plaintext fw-bold"
                        style={{
                          color: colors.primary,
                          fontSize: "18px",
                        }}
                      >
                        ${pricePerShare > 0 ? pricePerShare.toFixed(2) : "0.00"}
                      </div>
                    </div>

                    <div className="form-group mb-4">
                      <label
                        className="form-label fw-semibold mb-2"
                        style={{ color: colors.textDark }}
                      >
                        Shares You Will Receive
                      </label>
                      <div
                        className="form-control-plaintext fw-bold"
                        style={{
                          color: colors.primary,
                          fontSize: "18px",
                          backgroundColor: "white",
                          padding: "12px",
                          borderRadius: "8px",
                          border: `1px solid ${colors.border}`,
                        }}
                      >
                        {typeof shares === "number" && shares > 0
                          ? shares.toFixed(0)
                          : 0}{" "}
                        shares
                      </div>
                    </div>

                    <button
                      type="submit"
                      className="btn w-100 mt-2 fw-semibold"
                      style={{
                        backgroundColor: colors.primary,
                        color: "white",
                        border: "none",
                        padding: "12px 24px",
                        borderRadius: "8px",
                        fontSize: "16px",
                        transition: "all 0.2s ease",
                      }}
                      onMouseEnter={(e) =>
                        (e.target.style.backgroundColor = colors.primaryHover)
                      }
                      onMouseLeave={(e) =>
                        (e.target.style.backgroundColor = colors.primary)
                      }
                    >
                      Confirm Investment
                    </button>
                  </form>
                )}
              </div>
            </div>
          </div>
        </div>
      </ModalContainer>
    </div>
  );
};

export default InvestNowPopup;
